import React from 'react'
import PropTypes from "prop-types";
import styled from 'styled-components'

const RadioContainer = styled.div`
  padding: 16px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  user-select: none;
  color: #111111;
  &:hover{
    color: #07A952;
  }
`;
const RadioOuterCircle = styled.div`
  width: 36px;
  height: 36px;
  min-width: 36px;
  min-height: 36px;
  border: ${props => props.isActive ? '1.5px solid #000000' : '1.5px solid #07A952'};
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.1s linear;
`;
const RadioInnerCircle = styled.div`
  width: ${props => props.isActive ? '0' : '28px'};
  height: ${props => props.isActive ? '0' : '28px'};
  border-radius: 50%;
  background-color: #07A952;
  transition: all 0.1s linear;
`;
const RadioLabel = styled.div`
  font-size: 22px;
  line-height: 27px;
  padding-left: 32px;
  flex-grow: 1;
  font-family: Montserrat;
  transition: all 0.3s linear;
`;

function Radio({ selected, onChange, label, value }) {
  return (
    <RadioContainer onClick={() => { onChange(value) }}>
      <RadioOuterCircle isActive={value !== selected}>
        <RadioInnerCircle isActive={value !== selected} />
      </RadioOuterCircle>
      <RadioLabel>{label}</RadioLabel>
    </RadioContainer>
  );
}

Radio.propTypes = {
  label: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  selected: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired
};

export default Radio;
