import React from 'react'
import PropTypes from "prop-types";
import styled from 'styled-components'
import Result_1 from './result/result-1.jpg'
import Result_2 from './result/result-2.jpg'
import Result_3 from './result/result-3.jpg'
import Result_4 from './result/result-4.jpg'

const ResultContainer = styled.div`
  padding: 32px 64px 92px 64px;
  background: #FFFFFF;
  box-shadow: 0px 8px 4px rgba(9, 9, 9, 0.12);
  border-radius: 24px;
  width: 100%;
  max-width: 1068px;
  h3{
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #959595;
    margin-bottom: 32px;
    text-align: center;
    margin-top: 0;
  }
`;
const ResultButton = styled.button`
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
  color: #2E2E2E;
  padding: 20px 60px;
  background: #FFFFFF;
  border-radius: 8px;
  border: none;
  margin-top: 56px;
  cursor: pointer;
  transition: 0.3s ease-out all;
  &:hover{
    background: #07A952;
    border-color: #07A952;
    color: #FFFFFF;
  }
  &:active{
    background: rgba(7, 169, 82, 0.8);
    border-color: rgba(7, 169, 82, 0.8);
  }
`;
const ResultItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  div{
    padding-right: 70px;
    flex-grow: 1;
    p{
      font-family: Arsenal;
      font-style: normal;
      font-weight: bold;
      font-size: 40px;
      line-height: 50px;
      color: #111111;
      margin-bottom: 32px;
    }
    span{
      font-family: Montserrat;
      font-style: normal;
      font-weight: normal;
      font-size: 22px;
      line-height: 32px;
      color: #111111;
    }
  }
  img{
    border-radius: 12px;
    width: 285px;
    min-width: 285px;
    height: 374px;
    display: block;
  }
`;

function Result({ resultValue, resetButton }) {
  let arr = Object.values(resultValue);
  let max = Math.max(...arr);
  const getKeyByValue = (object, value) => {
    return Object.keys(object).find(key => object[key] === value)
  }
  const result = getKeyByValue(resultValue, max)
  const getresultData = () => {
    switch (+result) {
      default:
      case 1: {
          return {
            title: 'Селянин-кріпак.',
            description: 'Ти кріпак. Належиш поміщику. Маєш власну земельну ділянку, проте, як мінімум, три дні на тиждень мусиш працювати на землі свого власника. У тебе невеликий бізнес - возиш волами різноманітні товари, за що платиш відсоток із прибутку поміщику. В будь-який час власник може тебе продати.',
            image: Result_1,
          }
      }
      case 2: {
          return {
            title: 'Міщанин.',
            description: 'Ти міщанин. У тебе невелика ювелірна майстерня і чимало замовлень, бо професію ти здобув у Швейцарії. Навіть імператорський двір лагодить у тебе коштовності. Маєш невеликий рахунок в банку, де заощаджуєш гроші для купівлі землі. Та придбати ділянку складно, адже ти не дворянського роду.',
            image: Result_2,
          }
      }
      case 3: {
          return {
            title: 'Купець.',
            description: 'Ти купець першої гільдії з капіталом від 30 тис. рублів (210 тис. дол). Платиш   податки. Володієш магазином або навіть невеличким заводиком.  Маєш будинок у центрі міста. Твій син навчається в університеті, а донька - в гімназії. Мрієш про статус дворянина, але отримати його дуже складно. Ти володієш авторитетом у суспільстві, адже займаєшся благодійністю.',
            image: Result_3,
          }
      }
      case 4: {
          return {
            title: 'Дворянин.',
            description: 'Ви спадковий дворянин. З дитинства виховувалися у батьківському маєтку. Отримали блискучу домашню освіту, а у спадок - 5000 десятин (6000 га) землі, 3000 душ кріпаків та скромний палац під Києвом. За час служби в армії здобули офіцерське звання. Згодом обійняли посаду предводителя повітового дворянства. Звільнені від сплати податків. Ваш син служить в посольстві у Відні.',
            image: Result_4,
          }
      }
      }
  }
  const resultData = getresultData()
  return (
    <>
      <ResultContainer>
        <h3>Ваш результат:</h3>
        <ResultItem>
          <div>
            <p>{resultData.title}</p>
            <span>{resultData.description}</span>
          </div>
          <img src={resultData.image} alt={resultData.title} />
        </ResultItem>
      </ResultContainer>
      <ResultButton onClick={resetButton}>Перейти на головну</ResultButton>
    </>
  );
}

Result.propTypes = {
  resultValue: PropTypes.object.isRequired,
  resetButton: PropTypes.func.isRequired,
};

export default Result;
